import Vue from "vue";
import Vuex from "vuex";
import { stat } from "fs";
import { shopsgoods, shopsinfo } from "./request/api";
import { toUnicode } from "punycode";
Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    name: "",
    latitude: 40.10038, //
    longitude: 116.3687, //
    address: {}, //
    categorys: [], //
    shops: [], //
    userInfo: {},
    shopsinfo: {},
    goods: [], //
    foodcar: []
  },

  // this.$store.commit()
  //
  mutations: {
    //state
    re_address(state) {
      //
    },
    re_categorys(state) {
      //
    },
    re_shops(state, res) {
      //
      state.goods = res.data;
    },
    re_userInfo(state, user) {
      state.userInfo.id = user.id;
      state.userInfo.phone = user.phone;
      state.userInfo.name = user.name;
    },
    dele_userfo(state) {
      state.userInfo = {};
    },
    del_foodcount(state, food) {
            //
      let b = state.foodcar.find(el => el.name == food.name);
      if (b) {
        b.count--;
      }
      if (b.count == 0) {
        let a = state.foodcar.indexOf(b);
        state.foodcar.splice(a, 1);
      }

    },
    add_foodcount(state, food) {
          //
      if (state.foodcar.length == 0) {
        food.count++;
        state.foodcar.push(food);
        return;
      }

      let b = state.foodcar.find(el => el.name == food.name);
      if (b) {
        b.count++;
      }
      if (!b) {
        food.count++;
        state.foodcar.push(food);
      }
  
    },
    setshopsinfo(state, res) {
      state.shopsinfo = res.data;
    },
    delall1(state){
      state.foodcar.forEach(el=>{el.count=0})
      state.foodcar = []      
    }
  },
  getters: {
    userInfoID: state => {
      return state.userInfo.id;
    },
    userInfophone: state => {
      return state.userInfo.phone;
    },
    foodcount: state => {
      let num = 0;
      state.foodcar.forEach(el => {
        num = num + el.count;
      });
      return num;
    }
  },
  actions: {
    //
    //
    // text(store){}
    //dispatch()
    shopsGoods(vs) {
      return new Promise((resolve, reject) => {
        shopsgoods()
          .then(res => {
            vs.commit("re_shops", res);
            resolve();
            console.log(res);
          })
          .catch(err => {
            console.log(err + "");
          });
      });
    },
    updata_foodCount({ commit }, { flag, food }) {
      // count,count
      if (!food.count) {
        Vue.set(food, "count", 0);
      }
      //flag
      if (flag) {
        commit("add_foodcount", food);
      } else {
        commit("del_foodcount", food);
      }
    },
    shopinfo({ commit }) {
      //shopHead
      shopsinfo()
        .then(res => {
          commit("setshopsinfo", res);
        })
        .catch(err => {
          console.log(err);
        });
    }
  }
});
